import Gustav from './Gustav.png'
import React, {useState} from 'react'

export default function Idle1() {

    const [coffee, setCoffee] = useState(0)
    let nid= false;
    const Sex = () => {
        let rand = Math.floor(Math.random() * 100)
        setTimeout(() => {return (<h1>+1</h1>) }, 3000)
        nid = false;
        return(null)
        // if (nid) {return (<h1>+1</h1>)}
        // else return null;
    }

    const handleClick = () => {
        setCoffee(coffee+1)
    }

    const deepfry = {
        filter: 'saturate(' + coffee + ')',
        fontSize:coffee + 'px',
    }

    return (
    
        <div className = 'container'>

    <div className = 'idle1'>
        <h2>Coffee breaks: {coffee}</h2>
        <img src = {Gustav} alt = 'gustav' onClick={handleClick} style={deepfry}/>
        {coffee < 10 ? <h2>Gustavclicker official</h2> : <h2 style = {{fontSize:coffee}}>Coffee break :PPPPP</h2>}
        <button onClick={() => {console.log(nid)}}/>
        <Sex/>
    </div>
    </div>
    )
}