import './App.css';
import Logo from './logo.png'
import Idle1 from './Idle1';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

export default function App() {
  return (
    <Router>
      <div>
        <div className='nav'>
          <img src = {Logo} alt = ''/>
          <Link to ='/idle1'>Idle 1</Link>
          <Link to ='/idle2'>Idle 2</Link>
          <Link to ='/idle3'>Idle 3</Link>
        </div>

        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route path="/idle1">
            <Idle1 />
          </Route>
          <Route path="/idle3">
            <Idle3 />
          </Route>
          <Route path="/">
            <Idle2 />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

